.board-row, .w3-third, .chooseButton {
    height: 80px;
}

.codewars {
    background-image: url("https://www.codewars.com/users/mthdht/badges/large");
    background-size: contain;
    background-repeat: no-repeat;
    width:200px;
    height: 25px;
}
.game {
    height: calc(100vh - 52px);
}

.game-board {
    height: 70%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}

.game, .game-board, .game-info {
    width: 100%;
}

@media screen and (min-width: 600px) {
    .game, .game-board, .bottombar {
        width: 600px;
    }

    .game {
        max-width:600px;
    }

    .board-row, .w3-third, .chooseButton {
        height: 120px;
    }
}


